import React, { useState } from 'react';
import '../Where.css';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth<800;

const CutImage  = React.memo(
    (props)=>{
        let [selectSector, setSelectSector] = useState('');
        let {sector, selectDate, holderPart, event} = props
        let freeSeat = {}

        let free = 0;
        for(let i1 = 0; i1 < sector[selectDate]['Балкон'].length; i1++){
            for(let i2 = 0; i2 < sector[selectDate]['Балкон'][i1].length; i2++){
                if(sector[selectDate]['Балкон'][i1][i2].status==='free'&&sector[selectDate]['Балкон'][i1][i2].type==='seat')
                    free+=1
            }
        }
        freeSeat['Балкон'] = free;

        free = 0;
        for(let i1 = 0; i1 < sector[selectDate]['Партер'].length; i1++){
            for(let i2 = 0; i2 < sector[selectDate]['Партер'][i1].length; i2++){
                if(sector[selectDate]['Партер'][i1][i2].status==='free'&&sector[selectDate]['Партер'][i1][i2].type==='seat')
                    free+=1
            }
        }
        freeSeat['Партер'] = free;
        return (
            <>
            {width ?
                <svg className='svg1 dont-select-text'  viewBox='0 0 400 250' preserveAspectRatio='xMinYMin meet' xmlns='http://www.w3.org/2000/svg'>
                    <g>
                        <rect x={-1} y={-1} width={392} height={232} id="canvas_background" fill="#ffffff" />
                        <g id="canvasGrid" display="none">
                            <rect id="svg_1" width="100%" height="100%" x={0} y={0} strokeWidth={0} fill="url(#gridpattern)" />
                        </g>
                    </g>
                    <g>
                        <g className='pointer-text'>
                            <title>
                                Свободно: {freeSeat['Балкон']}
                            </title>
                            <rect stroke="#191e28" onClick={()=>{setSelectSector('Балкон'); holderPart('Балкон', event)}}  fill={selectSector==='Балкон'?'red':'#191e28'} strokeWidth="1.5" x="11.499992" y="156.173295" width={371} height={56} id="svg_4" rx={5} />
                        </g>
                        <g className='pointer-text'>
                            <title>
                                Свободно: {freeSeat['Партер']}
                            </title>
                            <rect onClick={()=>{setSelectSector('Партер'); holderPart('Балкон', event)}}  fill={selectSector==='Партер'?'red':'#191e28'} strokeWidth="1.5" x="10.50002" y="47.173296" width={371} height={105} id="svg_18" stroke="#191e28" rx={5} />
                        </g>
                        <rect fill="#ffd600" stroke="#ffd600" strokeWidth="1.5" x="41.500001" y="20.173296" width={308} height={19} id="svg_22" rx={5} />
                        <rect fill="#ffd600" stroke="#ffd600" strokeWidth="1.5" x="752.500001" y="158.173296" width={1} height={0} id="svg_23" />
                        <text fill="#000000" stroke="#ffd600" strokeWidth={0} x="176.500001" y="34.173296" id="svg_25" fontSize={11} fontFamily="Helvetica, Arial, sans-serif" textAnchor="start" xmlSpace="preserve">сцена</text>
                        <text fill="#fcfcfc" stroke="#ffd600" strokeWidth={0} x="173.500001" y="105.173296" id="svg_26" fontSize={11} fontFamily="Helvetica, Arial, sans-serif" textAnchor="start" xmlSpace="preserve" className='pointer-text' onClick={()=>{setSelectSector('Партер'); holderPart('Балкон', event)}}>Партер</text>
                        <text fill="#fcfcfc" stroke="#ffd600" strokeWidth={0} x="173.500001" y="190.173296" id="svg_28" fontSize={11} fontFamily="Helvetica, Arial, sans-serif" textAnchor="start" xmlSpace="preserve" className='pointer-text' onClick={()=>{setSelectSector('Балкон'); holderPart('Балкон', event)}}>Балкон</text>
                    </g>
                </svg>
                :
                <svg className='dont-select-text' width={390} height={230} xmlns='http://www.w3.org/2000/svg'>
                    <g>
                        <rect x={-1} y={-1} width={392} height={232} id="canvas_background" fill="#ffffff" />
                        <g id="canvasGrid" display="none">
                            <rect id="svg_1" width="100%" height="100%" x={0} y={0} strokeWidth={0} fill="url(#gridpattern)" />
                        </g>
                    </g>
                    <g>
                        <g className='pointer-text'>
                            <title>
                                Свободно: {freeSeat['Балкон']}
                            </title>
                            <rect stroke="#191e28" onClick={()=>{setSelectSector('Балкон'); holderPart('Балкон', event)}}  fill={selectSector==='Балкон'?'red':'#191e28'} strokeWidth="1.5" x="11.499992" y="156.173295" width={371} height={56} id="svg_4" rx={5} />
                        </g>
                        <g className='pointer-text'>
                            <title>
                                Свободно: {freeSeat['Партер']}
                            </title>
                            <rect onClick={()=>{setSelectSector('Партер'); holderPart('Балкон', event)}}  fill={selectSector==='Партер'?'red':'#191e28'} strokeWidth="1.5" x="10.50002" y="47.173296" width={371} height={105} id="svg_18" stroke="#191e28" rx={5} />
                        </g>
                        <rect fill="#ffd600" stroke="#ffd600" strokeWidth="1.5" x="41.500001" y="20.173296" width={308} height={19} id="svg_22" rx={5} />
                        <rect fill="#ffd600" stroke="#ffd600" strokeWidth="1.5" x="752.500001" y="158.173296" width={1} height={0} id="svg_23" />
                        <text fill="#000000" stroke="#ffd600" strokeWidth={0} x="176.500001" y="34.173296" id="svg_25" fontSize={11} fontFamily="Helvetica, Arial, sans-serif" textAnchor="start" xmlSpace="preserve">сцена</text>
                        <text fill="#fcfcfc" stroke="#ffd600" strokeWidth={0} x="173.500001" y="105.173296" id="svg_26" fontSize={11} fontFamily="Helvetica, Arial, sans-serif" textAnchor="start" xmlSpace="preserve" className='pointer-text' onClick={()=>{setSelectSector('Партер'); holderPart('Балкон', event)}}>Партер</text>
                        <text fill="#fcfcfc" stroke="#ffd600" strokeWidth={0} x="173.500001" y="190.173296" id="svg_28" fontSize={11} fontFamily="Helvetica, Arial, sans-serif" textAnchor="start" xmlSpace="preserve" className='pointer-text' onClick={()=>{setSelectSector('Балкон'); holderPart('Балкон', event)}}>Балкон</text>
                    </g>
                </svg>
            }
            </>
        )
    }
)

export default CutImage;
