import React, { useState } from 'react';
import DvorecSporta from '../../dvorecsporta.png';
import '../Where.css';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import { mainWindow } from '../../App'
const width = mainWindow.current.offsetWidth<800;

const CutImage  = React.memo(
    (props)=>{
        let [selectSector, setSelectSector] = useState('');
        let [show, setShow] = useState(false);
        let {sector, selectDate, holderPart, event} = props
        return (
            <div className='column'>
                {show?
                    <Lightbox
                        mainSrc={DvorecSporta}
                        onCloseRequest={() => setShow(false)}
                    />
                     :null}
                {width ?
                    <svg className='svg1 dont-select-text'  viewBox='0 0 390 250' preserveAspectRatio='xMinYMin meet' xmlns="http://www.w3.org/2000/svg">
                        {/* Created with Method Draw - http://github.com/duopixel/Method-Draw/ */}
                        <g>
                            <rect fill="#f8f8f8" id="canvas_background" height={282} width={392} y={-1} x={-1} />
                            <g display="none" overflow="visible" y={0} x={0} height="100%" width="100%" id="canvasGrid">
                                <rect fill="url(#gridpattern)" strokeWidth={0} y={0} x={0} height="100%" width="100%" />
                            </g>
                        </g>
                        <g>
                            <rect onClick={()=>{setSelectSector('Партер'); holderPart('Партер', event)}} className='pointer-text' rx={5} stroke="#191e28" id="svg_4" height={80} width={371} y="39.173296" x="6.499992" strokeWidth="1.5" fill={selectSector==='Партер'?'red':'#191e28'} />
                            <rect onClick={()=>{setSelectSector('Амфитеатр'); holderPart('Амфитеатр', event)}} className='pointer-text' rx={5} stroke="#191e28" id="svg_18" height={105} width={371} y="124.173296" x="6.50002" strokeWidth="1.5" fill={selectSector==='Амфитеатр'?'red':'#191e28'} />
                            <rect rx={5} id="svg_22" height={19} width={218} y="8.173296" x="79.500001" strokeWidth="1.5" stroke="#ffd600" fill="#ffd600" />
                            <rect id="svg_23" height={0} width={1} y="158.173296" x="752.500001" strokeWidth="1.5" stroke="#ffd600" fill="#ffd600" />
                            <text xmlSpace="preserve" textAnchor="start" fontFamily="Helvetica, Arial, sans-serif" fontSize={11} id="svg_25" y="21.173296" x="172.500001" strokeWidth={0} stroke="#ffd600" fill="#000000">сцена</text>
                            <text xmlSpace="preserve" textAnchor="start" fontFamily="Helvetica, Arial, sans-serif" fontSize={11} id="svg_26" y="182.173296" x="162.500001" strokeWidth={0} stroke="#ffd600" onClick={()=>{setSelectSector('Амфитеатр'); holderPart('Амфитеатр', event)}} className='pointer-text' fill="#fcfcfc">Амфитеатр</text>
                            <text xmlSpace="preserve" textAnchor="start" fontFamily="Helvetica, Arial, sans-serif" fontSize={11} id="svg_28" y="87.173296" x="170.500001" strokeWidth={0} stroke="#ffd600" onClick={()=>{setSelectSector('Партер'); holderPart('Партер', event)}} className='pointer-text' fill="#fcfcfc">Партер</text>
                        </g>
                    </svg>

                    :
                    <svg width={390} height={280} xmlns="http://www.w3.org/2000/svg">
                        <g>
                            <rect fill="#f8f8f8" id="canvas_background" height={282} width={392} y={-1} x={-1} />
                            <g display="none" overflow="visible" y={0} x={0} height="100%" width="100%" id="canvasGrid">
                                <rect fill="url(#gridpattern)" strokeWidth={0} y={0} x={0} height="100%" width="100%" />
                            </g>
                        </g>
                        <g>
                            <rect onClick={()=>{setSelectSector('Партер'); holderPart('Партер', event)}} className='pointer-text' rx={5} stroke="#191e28" id="svg_4" height={80} width={371} y="39.173296" x="6.499992" strokeWidth="1.5" fill={selectSector==='Партер'?'red':'#191e28'} />
                            <rect onClick={()=>{setSelectSector('Амфитеатр'); holderPart('Амфитеатр', event)}} className='pointer-text' rx={5} stroke="#191e28" id="svg_18" height={105} width={371} y="124.173296" x="6.50002" strokeWidth="1.5" fill={selectSector==='Амфитеатр'?'red':'#191e28'} />
                            <rect rx={5} id="svg_22" height={19} width={218} y="8.173296" x="79.500001" strokeWidth="1.5" stroke="#ffd600" fill="#ffd600" />
                            <rect id="svg_23" height={0} width={1} y="158.173296" x="752.500001" strokeWidth="1.5" stroke="#ffd600" fill="#ffd600" />
                            <text xmlSpace="preserve" textAnchor="start" fontFamily="Helvetica, Arial, sans-serif" fontSize={11} id="svg_25" y="21.173296" x="172.500001" strokeWidth={0} stroke="#ffd600" fill="#000000">сцена</text>
                            <text xmlSpace="preserve" textAnchor="start" fontFamily="Helvetica, Arial, sans-serif" fontSize={11} id="svg_26" y="182.173296" x="162.500001" strokeWidth={0} stroke="#ffd600" onClick={()=>{setSelectSector('Амфитеатр'); holderPart('Амфитеатр', event)}} className='pointer-text' fill="#fcfcfc">Амфитеатр</text>
                            <text xmlSpace="preserve" textAnchor="start" fontFamily="Helvetica, Arial, sans-serif" fontSize={11} id="svg_28" y="87.173296" x="170.500001" strokeWidth={0} stroke="#ffd600" onClick={()=>{setSelectSector('Партер'); holderPart('Партер', event)}} className='pointer-text' fill="#fcfcfc">Партер</text>
                        </g>
                    </svg>
                }<br/>
                <div className='plan-zala' onClick={()=>{setShow(true)}}>Открыть схему зала</div>
            </div>
        )
    }
)

export default CutImage;
