import React, { useState } from 'react';
import { abc } from '../../redux/constants/other';
import './RenderSeats.css';
import Tooltip from 'react-simple-tooltip'

const CutImage  = (
    (props)=>{
        let i;
        let {seats, price, selectDate, selectPart, holderSeats, event } = props;
        return (
            <>
            {seats[selectDate][selectPart]!==undefined?
                seats[selectDate].withoutNew!==undefined?
                    <>
                    <div className="order-first-stage">
                        <div className="content-stage" style={{overflowY: 'hidden'}}>
                            <div className="stage-sector">
                                {
                                    seats[selectDate][selectPart].map(
                                        (element, idx)=>
                                            <div key={idx} className="table-tickets-order">
                                                <div className="ticket-order-row">
                                                    <div className="person" style={{overflow: 'hidden', wordWrap: 'break-word'}}><b>{element.name}</b><br/>Свободно:&nbsp;{element.free-element.sold}</div>
                                                    <div className="price" style={{overflow: 'hidden', wordWrap: 'break-word'}}>Цена: <b>{element.price.price} сом</b></div>
                                                    &nbsp;
                                                    &nbsp;
                                                    <div className='amount-ticket' style={{overflow: 'hidden', wordWrap: 'break-word'}}>
                                                        <div style={{border: '2px solid #ffc500', fontWeight: 'bold'}}  className='amount-control amount-minus'
                                                             onClick={()=>{
                                                                 let del = true
                                                                 for(let i=0; i<seats[selectDate][selectPart].length; i++){
                                                                     if(seats[selectDate][selectPart][i].name===element.name){
                                                                         seats[selectDate][selectPart][i].sold-=1
                                                                     }
                                                                 }
                                                                 event.where.data = seats;
                                                                 let selectElem = {...element}
                                                                 selectElem.newWithout = true
                                                                 selectElem.price = selectElem.price.price
                                                                 holderSeats(selectElem, event, selectPart, del)
                                                             }}
                                                        >
                                                            -
                                                        </div>
                                                        <div style={{border: '2px solid #ffc500', fontWeight: 'bold'}} className='amount-control amount-plus'
                                                                onClick={()=>{
                                                                    let del = false
                                                                    for(let i=0; i<seats[selectDate][selectPart].length; i++){
                                                                        console.log(seats[selectDate][selectPart][i].name, element.name)
                                                                        if(seats[selectDate][selectPart][i].name===element.name){
                                                                            seats[selectDate][selectPart][i].sold+=1
                                                                        }
                                                                    }
                                                                    event.where.data = seats;
                                                                    let selectElem = {...element}
                                                                    selectElem.newWithout = true
                                                                    selectElem.price = selectElem.price.price
                                                                    holderSeats(selectElem, event, selectPart, del)
                                                                }}
                                                        >
                                                            +
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                    )
                                }
                            </div>
                        </div>
                    </div>

                    </>
                    :
                    <div style={{width: '100%', marginBottom: '20px'}}>
                        {price!==undefined&&price.length>0?
                            <div className='part-div-price'>
                                <div className='ola'>
                                    <div className='part-empty' style={{background: 'red'}}/>
                                    <div className='part-price'>
                                        {'Продано'}
                                    </div>
                                </div>
                                {
                                    price.map((element, idx) => {
                                        return(
                                            <div key={idx} className='ola'>
                                            <div className='part-empty' style={{background: element.color}}/>
                                            <div className='part-price'>
                                                {element.price + ' сом'}
                                            </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            :
                            null}
                        <div style={{width: ((seats[selectDate][selectPart][0].length)*26+150)+'px'}}>
                            {seats[selectDate][selectPart].map((row, rowId) => {
                            i=1;
                            for(let i1 = 0; i1 < row.length; i1++){
                                if(row[i1].type!=='empty')
                                    i+=1
                            }
                            return (
                                <div key={rowId}>
                                    <div className='part-number'>
                                        {'Ряд: ' + (row[0].name.split(':')[0].split(' ')[1])}
                                    </div>
                                    {
                                        row.map((place, placeId) => {
                                            if (place.type === 'seat' && place.status !== 'reserve') {
                                                i-=1;
                                                return (
                                                    <Tooltip key={placeId} style={{zIndex: '1000', fontSize: '13px'}} content={'Ряд: '+seats[selectDate][selectPart][rowId][placeId].name.split(':')[0].split(' ')[1]
                                                    +' Место: '+seats[selectDate][selectPart][rowId][placeId].name.split(':')[1].split(' ')[0]
                                                    +' Стоимость: '+place.price}
                                                    >
                                                        <div onClick={() => {
                                                            if(place.status !== 'block' && place.status !== 'sold' && place.status !== 'reserve'){
                                                                let del = false
                                                                if(place.color!=='red') {
                                                                    del = false
                                                                    seats[selectDate][selectPart][rowId][placeId].color = 'red'
                                                                    seats[selectDate][selectPart][rowId][placeId].status = 'hold'
                                                                } else {
                                                                    del = true
                                                                    seats[selectDate][selectPart][rowId][placeId].status = 'free'
                                                                    for(let i = 0; i< price.length; i++){
                                                                        if(price[i].price===place.price)
                                                                            seats[selectDate][selectPart][rowId][placeId].color = price[i].color
                                                                    }
                                                                }
                                                                event.where.data = seats;
                                                                place.selectSector = abc[event.where.name][selectPart]
                                                                holderSeats(place, event, selectPart, del)
                                                            }
                                                        }} className='part' style={{background: place.color}}>
                                                            <div className='part-text'>
                                                                {place.name.split(':')[1].split(' ')[0]}
                                                            </div>
                                                        </div>
                                                    </Tooltip>
                                                )
                                            } else if(place.status == 'reserve'){
                                           return(
                                               <Tooltip style={{fontSize: '13px'}} content={place.color!=='red'?'Места: '+seats[selectDate][selectPart][rowId][placeId].name+(place.color!=='gray'?' Цена: '+seats[selectDate][selectPart][rowId][placeId].price:''):'Продано'}>
                                                   <div className='part' style={{background: place.color}}>
                                                       <div className='part-text'>
                                                           {place.name.split(':')[1].split(' ')[0]}
                                                       </div>
                                                   </div>
                                               </Tooltip>
                                           )
                                            } else
                                                return (
                                                    <div className='part-empty' style={{background: place.color}}/>
                                                )
                                        })
                                    }
                                </div>
                            )
                        })}
                        </div>
                    </div>

                :
                null
            }
            </>
        )
    }
)

export default (CutImage);
